import utils from '../base/utils';

let canvas = canvas || (() => {
  return {
    init(particles) {
      if (particles) {
        particlesJS.load('particles-js', '/assets/json/particles.json', () => {
          window.setTimeout(() => {
            utils.addClass(particles, 'is-visible');
          }, 300);
        });
      }
    }
  };
})();

canvas.init(document.getElementById('particles-js'));

export default canvas;
