import utils from '../base/utils';
import canvas from '../modules/canvas';

let navigation = navigation || (() => {
  const body = document.querySelector('body');

  return {
    init() {
      this.binding();
    },

    binding() {
      utils.on('click', '.js-nav-to-pages', function(e) {
        if (history.pushState) {
          e.preventDefault();
          history.pushState({ajaxNav: window.location.href}, '', window.location.href);
          window.setTimeout(() => {
            if (document.querySelector('.js-intro-loader')) {
              utils.addClass(document.querySelector('.js-intro-loader'), 'is-visible');
            }
          }, 1000);
          if (!utils.hasClass(body, 'homepage')) {
            utils.addClass(body, 'homepage');
          }
          navigation.ajaxRequest(this.getAttribute('href'), navigation.goToPages);
        }
      });

      window.addEventListener('load', () => {
        window.setTimeout(() => {
          window.addEventListener('popstate', (e) => {
            if (history.state !== null) {
              navigation.ajaxRequest(window.location.href, navigation.loadContent);
              if (e.state.ajaxNav === document.location.origin + '/') {
                window.setTimeout(() => {
                  canvas.init(document.getElementById('particles-js'));
                }, 500);
              }
            }
          });
        }, 0);
      });
    },

    ajaxRequest(url, callback) {
      const request = new XMLHttpRequest();
      request.open('GET', url, true);
      request.responseType = 'document';

      request.onload = () => {
        if (request.status >= 200 && request.status < 400) {
          const content = request.response.body.innerHTML;
          const title = request.response.title;
          callback(url, content, title);
        }
        else {
          this.navError(request);
        }
      };

      request.onerror = () => {
        window.location.href = url;
      };

      request.send();
    },

    goToPages(url, content, title) {
      const intro = document.getElementById('js-intro');
      utils.addClass(intro, 'is-hidden');
      utils.addClass(document.querySelector('.mainNav.-hp'), 'is-hidden');
      window.setTimeout(() => {
        utils.removeElement(intro);
        document.title = title;
        body.innerHTML = content;
        window.setTimeout(() => {
          utils.removeClass(body, 'homepage');
          window.scrollTo(0, 0);
        }, 200);
      }, 600);
      history.pushState({ajaxNav: url}, '', url);
    },

    loadContent(url, content, title) {
      body.innerHTML = content;
      document.title = title;
    },

    navError(request) {
      if (request.responseText) {
        alert(request.responseText);
      }
      else {
        alert('Une erreur s\'est produite. Rechargez la page.');
      }
      window.location.reload();
    }
  };
})();

navigation.init();
