import utils from '../base/utils';

let fonts = fonts || {
  init() {
    if (document.documentElement.className.indexOf('fonts-loaded' ) > -1) {
      return;
    }

    const fontA = new FontFaceObserver('Playfair Display', {
      weight: '400',
      style: 'italic'
    });
    const fontB = new FontFaceObserver('Playfair Display', {
      weight: '700'
    });
    const fontC = new FontFaceObserver('Fira Sans', {
      weight: 400
    });
    const fontD = new FontFaceObserver('Fira Sans', {
      weight: 400,
      style: 'italic'
    });
    const fontE = new FontFaceObserver('Fira Sans', {
      weight: 700
    });
    const fontF = new FontFaceObserver('Fira Mono', {
      weight: 400
    });
    const fontG = new FontFaceObserver('Fira Mono', {
      weight: 700
    });

    window.addEventListener('load', () => {
      Promise.all([
        fontA.check(), fontB.check(), fontC.check(), fontD.check(), fontE.check(), fontF.check(), fontG.check()
      ]).then(() => {
        document.documentElement.className += ' fonts-loaded';
        localStorage.setItem('fontsLoaded', true);
      });
    });
  }
};

fonts.init();
